import React, { Component } from 'react';
import CurrentService from './CurrentService';
import Util from '../common/Util';

export default ({
    categories
}) =>
{

            <div className="CurrentServices">
                <h2 className="currServ-title title">These are the services we are providing you at the moment:</h2>
                <div className="currentServices-sub">
                    {this.props.categories.map((item, i) => (
                        <p>{item}</p>
                    ))}
                </div>
                <style jsx>
                    {`
                        .CurrentServices {
                            margin-left: auto;
                            margin-right: auto;
                        }
                        .currentServices-sub {
                            display: grid;
                            grid-template-columns: repeat(auto-fit,minmax(340px,1fr));
                        }
                    `}
                </style>
            </div>

    }




